@import url(https://fonts.googleapis.com/css?family=Barrio);
@import url(https://fonts.googleapis.com/css?family=Flamenco);
*{
  margin: 0;
  box-sizing: border-box;
}
@media (max-width: 775px){
  .header{
    padding: 2% 0 2% 2%;
  }
  .header .header_logo{
    height: auto;
    width: 15%;
    background-color: white;
  }
  .header .header_title h1{
    font-size: 3rem;
  }
  .header .header_menu a{
    font-size: 1.5rem;
    margin-right: 25%;
  }
}
.header{
  height: 80px;
  width: 100%;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 50%;
  background-color: tomato;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.header_logo{
  height: auto;
  width: 6%;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 50%;
  margin: 0.5% 0 0.5% 0.5%;
  padding: 0 0 2% 0;
  background-color: white;
}
.header_logo img{
  height: auto;
  width: 100%;
}
.header_title{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.header_title h1{
  text-align: center;
  font-size: 4.5rem;
  font-family: "Barrio";
}
.header_menu{
  height: auto;
  width: 12.5%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 1%;
}
.header_menu a{
  font-size: 1.8rem;
  color: white;
  font-family: "Flamenco";
}
a{
  text-decoration: none;
  color: black;
}

.parent_view{
    height: auto;
    width: 100%;
    background-color: blueviolet;
}
.main_view1{
    height: auto;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
img{
    height: 700px;
    width: 95%;
    object-fit: cover;
}
.parent{
    height: auto;
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-gap: 7px 5px;
}
@media (max-width: 775px){
    .parent{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .parent .main_card{
        height: auto;
        width: 75%;
        margin: auto;
        margin-top: 6%;

    }
}
.main_card{
    height: auto;
    width: 92%;
    margin: 3% 0 0 4%;
    background-color: rgba(205, 255, 247, 0.897);
}
.img_card{
    height: auto;
    width: 100%;
}
.img_card img{ 
    height: auto;
    width: 100%;
}
.card_name{
    height: auto;
    width: auto;
    padding: 0 0 2% 2%;
}
.description{
    height: auto;
    width: auto;
    padding: 0 0 5% 2%;
}
.footer{
    height: auto;
    width: 100%;
    background-color: black;
    margin-top: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.footer h1{
    color: white;
}
.title{
    height: auto;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    padding-top: 1%;
    -webkit-justify-content: center;
            justify-content: center;
}
.title h1{
    font-size: 3rem;
    font-family: "Barrio";
}
.follow-us{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-right: 3%;
}
.follow-us a{
    margin-left: 3%;
    padding-top: 1%;
    padding-bottom: 1%;
}
